.select-box {
  line-height: normal!important;
  position: relative;
  height: 32px;
  width: 100%;
  background: #FFF;
}
.select-box .select-box-inner {
  width: 100%;
  height: 100%;
  position: relative;
  border: 1px solid #e6e7ec;
  padding: 6px 20px 6px 6px;
  cursor: pointer;
}
.select-box .select-box-inner:before {
  content: "";
  display: block;
  border: 5px solid transparent;
  border-top-color: #a0a0a0;
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -2px;
  z-index: 2;
}
.select-box .select-box-inner:after {
  content: "";
  display: block;
  width: 100%;
  top: 0;
  position: absolute;
  height: 100%;
  background-color: transparent;
  right: 0;
  z-index: 1;
}
.select-box .select-box-inner:hover {
  background: #e6e7ec;
  border: 1px solid #dadbe0;
}
.select-box .select-box-inner:hover .select-text {
  color: #111111;
}
.select-box .select-box-inner:hover:before {
  border-top-color: #787878;
}
.select-box .select-box-inner .select-text {
  border: none;
  width: 100%;
  background: transparent;
  cursor: pointer;
  color: #333333;
}
.select-box .select-box-inner .select-text.placeholder {
  color: #999999;
}
.select-box.combo .select-box-inner:after {
  width: 20px;
}
.select-box dl {
  display: none;
  border: 1px solid #e6e7ec;
  background: #FFF;
  position: absolute;
  min-width: 100%;
  max-width: 200%;
  max-height: 360px;
  margin-top: -1px;
  z-index: 10000;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 5px 0 10px;
}
.select-box dl dd {
  white-space: nowrap;
  height: 26px;
  line-height: 26px;
  padding: 0 6px;
  max-width: 200%;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
.select-box dl dd:hover,
.select-box dl dd.on {
  background: #f4f5f9;
}
.select-box.disabled {
  background: #e6e7ec;
}
.select-box.disabled .select-box-inner {
  cursor: default;
  border: 1px solid #dadbe0;
}
.select-box.disabled .select-box-inner .select-text {
  cursor: default;
  color: #a5a6aa;
}
.select-box.disabled .select-box-inner:hover {
  background: transparent;
}
.select-box.disabled .select-box-inner:hover:before {
  border-top-color: #a0a0a0;
}
.select-box.disabled .select-box-inner:hover .select-text {
  color: #a5a6aa;
}
.select-box.disabled.hide-arrow .select-box-inner {
  padding: 6px;
}
.select-box.disabled.hide-arrow .select-box-inner:before,
.select-box.disabled.hide-arrow .select-box-inner:after {
  display: none;
}
.select-box.open dl {
  display: block;
}
select.origin-select {
  visibility: hidden;
}
.pt {
  position: absolute;
  text-align: left;
  background: none repeat scroll 0 0 #FFFFFF;
  border: 1px solid #CFCFCF;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
  z-index: 2209;
  width: auto;
  height: auto;
  display: none;
}
.pt .cont {
  height: auto;
  overflow-y: auto;
  background-color: #FFFFFF;
  padding: 10px;
}
.pt .out,
.pt .in {
  display: block;
  font-family: simsun;
  font-size: 0;
  height: 0;
  overflow: visible;
  position: absolute;
  width: 0;
}
/*************up*******************/
.pt-up .out,
.pt-up .in {
  border-style: dashed dashed solid;
}
.pt-up .out {
  border-color: transparent transparent #CFCFCF;
  border-width: 0 7px 8px;
  left: 28px;
  top: -8px;
}
.pt-up .in {
  border-color: transparent transparent #FFFFFF;
  border-width: 0 6px 7px;
  left: 29px;
  top: -7px;
}
/*************right****************/
.pt-right .out,
.pt-right .in {
  border-style: dashed dashed dashed solid;
}
.pt-right .out {
  border-color: transparent transparent transparent #CFCFCF;
  border-width: 8px 0 7px 7px;
  right: -7px;
  top: 28px;
}
.pt-right .in {
  border-color: transparent transparent transparent #FFFFFF;
  border-width: 7px 0 6px 6px;
  right: -6px;
  top: 29px;
}
/*************down*****************/
.pt-down .out,
.pt-down .in {
  border-style: solid dashed dashed;
}
.pt-down .out {
  border-color: #CFCFCF transparent transparent;
  border-width: 8px 7px 0;
  bottom: -8px;
  left: 28px;
  top: auto;
}
.pt-down .in {
  border-color: #FFFFFF transparent transparent;
  border-width: 7px 6px 0;
  bottom: -7px;
  left: 29px;
  top: auto;
}
/*************left*****************/
.pt-left .out,
.pt-left .in {
  border-style: dashed solid dashed dashed;
}
.pt-left .out {
  border-color: transparent #CFCFCF transparent transparent;
  border-width: 7px 8px 7px 0;
  left: -8px;
  top: 28px;
}
.pt-left .in {
  border-color: transparent #FFFFFF transparent transparent;
  border-width: 6px 7px 6px 0;
  left: -7px;
  top: 29px;
}
